.wrapper {
	position: relative;
	box-sizing: border-box;
	width: 100%;
	max-width: $wrapper-width;
	margin: 0 auto;
	padding: $section-padding-mobile $wrapper-horizontal-padding;

	@media only screen and (min-width: $tablet-min) {
		padding: $section-padding-tablet $wrapper-horizontal-padding;

		header section & {
			padding-top: 65px;
		}
	}

	@media only screen and (min-width: $desktop-min) {
		padding: $section-padding $wrapper-horizontal-padding;
	}

	&.wrapper-large {
		max-width: $large-wrapper-width;
	}

	&.small-pad {
		padding-top: $half-space;
		padding-bottom: $half-space;
	}
}

$siblings-height: 40px;
nav.siblings {
	background-color: $brand-color;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	overflow: hidden;

	.wrapper {
		display: flex;
		justify-content: flex-start;
		padding: 0 10px;
	}

	a {
		display: inline-block;
		padding: 0 10px;
		margin-right: 5px;
		height: $siblings-height;
		line-height: $siblings-height;
		color: rgba(255, 255, 255, 0.6);
		font-weight: bold;
		position: relative;
		z-index: 10;

		&:hover,
		&:active
		&.active:hover,
		&.active:active,
		&.active {
			color: rgba(255, 255, 255, 1);
		}
	}

	a.active,
	.no-active-replacement {
		position: relative;
		z-index: 9;

		&:before,
		&:after {
			content: "";
			display: block;
			position: absolute;
			height: 100%;
			left: 100%;
			width: 9999px;
			background: rgba(0, 0, 0, 0.4);
			top: 0;
			border-radius: 0 0 0 4px;
			pointer-events: none;
		}

		&:after {
			right: 100%;
			left: auto;
			border-radius: 0 0 4px 0;
		}
	}

	.no-active-replacement {
		&:before,
		&:after {
			border-radius: 0;
		}
	}

	&.light {
		a.active,
		.no-active-replacement {
			&:before,
			&:after {
				background: $brand-color;
			}
		}

		a {
			&:active
			&.active:hover,
			&.active:active,
			&.active {
				color: $brand-color;
				background: #fff;
			}
		}
	}

	+ header {
		padding-top: $siblings-height;
	}
}

.nav-bar.wrapper {
	padding-top: 20px;
	padding-bottom: 20px;
	font-size: $font-small;

	.logo a {
		display: inline-block;
	}

	a {
		text-decoration: none;
		white-space: nowrap;
		display: block;
	}

	.main,
	.right,
	.search {
		display: none;
		text-align: right;

		a {
			padding: 20px 0;
		}

		.nav-open & {
			display: block;
		}
	}

	.search {
		position: relative;
		margin: 20px 0 0 auto;
		$width-transition-duration: 500ms;
		$color-transition-duration: 200ms;


		input {
			@extend .search-input !optional;

			&:focus,
			&:not(:invalid) {
				+ .nav-cover {
					opacity: 1;
				}
			}
		}
	}

	.main {
		flex: 1;
		margin-top: 20px;

		a {
			transition: opacity $transition-duration ease;
			opacity: $text-opacity;

			&.active, &:hover {
				opacity: 1;
			}
		}
	}

	@media only screen and (max-width: $desktop-min - 1) {
		.right .button {
			background: none;
			text-align: right;
			text-transform: none;
			font-weight: 400;
			border: 0;

			&.dark:hover {
				color: #fff;
			}

			&.light:hover {
				color: $brand-color;
			}
		}
	}
}
